<template>
  <div class="column" v-shortkey="['ctrl', 'alt', 'o']" @shortkey.native="console.log('test')">
    <div v-if="displayType === 'modal'">
      <word-modal
        :emoji="emoji"
        :translation="translation"
        :category="category"
        :isActive.sync="isActive"
      ></word-modal>
      <a @click="isActive = true">
        <span>{{emoji}}</span>
      </a>
    </div>
    <div v-else-if="displayType === 'checkbox'">
      <a class="tooltip" :data-tooltip="translation"><span>{{ emoji }}</span></a>
    </div>
  </div>
</template>

<script>
import WordModal from '@/components/WordModal';

export default {
  name: 'word-item',
  props: {
    name: String,
    emoji: String,
    translation: String,
    category: String,
    displayType: String,
  },
  components: {
    WordModal,
  },
  data: () => ({
    isActive: false,
  }),
};
</script>

<style scoped>
span {
  font-size: 2.5rem;
}
.modal {
  -webkit-animation-duration: 0.5s;
  -webkit-animation-delay: 0s;
}
.tooltip:focus::after, .tooltip:hover::after {
  background-color: #7957d5;
}
</style>



